import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// Main component
import { TimesheetComponent } from './timesheet.component';

// Sub-components
import { DailyComponent } from './daily/daily.component';
import { WeeklyComponent } from './weekly/weekly.component';
import { ApprovalsComponent } from './approvals/approvals.component';
import { SettingTimesheetComponent } from './setting-timesheet/setting-timesheet.component';

// Child components of daily
import { AddTimeComponent } from './add-time/add-time.component';
import { EditTimeComponent } from './edit-time/edit-time.component';

const routes: Routes = [
  {
    path:'',
    component: TimesheetComponent,
    children: [
      {
        path:'',
        redirectTo: 'daily',
        pathMatch: 'full'
      },
      {
        path:'daily',
        component:DailyComponent,
      },
      {
        path:'add-time',
        component:AddTimeComponent,
      },
      {
        path:'edit-time',
        component:EditTimeComponent,
      },

      {
        path:'weekly',
        component:WeeklyComponent,
      },
      {
        path:'approvals',
        component:ApprovalsComponent,
      },
      {
        path:'setting',
        component:SettingTimesheetComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TimesheetRoutingModule { }
