import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddTimeComponent } from '../add-time/add-time.component';
import { TimesheetService } from 'src/app/core/services/timesheet.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';
import FileSaver from "file-saver";
import { saveAs } from "file-saver/dist/FileSaver";
@Component({
  selector: 'app-approvals',
  templateUrl: './approvals.component.html',
  styleUrls: ['./approvals.component.scss']
})
export class ApprovalsComponent implements OnInit {

  // Array of approvals
  // approvalTimeSheet = [
  //   {
  //     id: 1,
  //     employee: 'Employee1',
  //     description: 'Description',
  //     startDate: 'Friday, June 9 2023',
  //     endDate: 'Friday, June 9 2023',
  //     requestDate: 'Friday, June 9 2023',
  //   },
  //   {
  //     id: 2,
  //     employee: 'Employee2',
  //     description: 'Description',
  //     startDate: 'Friday, June 9 2023',
  //     endDate: 'Friday, June 9 2023',
  //     requestDate: 'Friday, June 9 2023',
  //   },
  //   {
  //     id: 3,
  //     employee: 'Employee3',
  //     description: 'Description',
  //     startDate: 'Friday, June 9 2023',
  //     endDate: 'Friday, June 9 2023',
  //     requestDate: 'Friday, June 9 2023',
  //   },
  //   {
  //     id: 4,
  //     employee: 'Employee4',
  //     description: 'Description',
  //     startDate: 'Friday, June 9 2023',
  //     endDate: 'Friday, June 9 2023',
  //     requestDate: 'Friday, June 9 2023',
  //   },
  // ]

  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public approved: boolean = false;
  public denied: boolean = false;
  public approvalId: any;
  public leaveApprovallist: any;
  public leavid: any;
  public leavid1: any;
  DataLink: any;
  public searchKey: any;

  constructor(private dialog: MatDialog,
    public timesheetService: TimesheetService,
    private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {

    this.getleaveApprovalList();
  }

  getleaveApprovalList() {
    this.timesheetService.getleaveapprovalList({}, this.currentUser.id).then((res) => {
      if (res) {
        this.leaveApprovallist = res.data;
      }
    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }

    });
  }

  getexpenseSearch(searchKey) {
    if (this.searchKey == "") {
      this.toastService.openErrorSnackBar("Please Enter Valid Employee Name!!!");
      this.timesheetService.getleaveapprovalSearch({}, this.currentUser.id,searchKey).then((res) => {
        if (res.success == true) {
          this.leaveApprovallist = res.data;
        }
      });
      

    }
    else{
      this.timesheetService.getleaveapprovalSearch({}, this.currentUser.id,searchKey).then((res) => {
        if (res.success == true) {
          this.leaveApprovallist = res.data;
        }
      }, (err) => {
        if (err.error.expose) {
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
  
      });
      
      
    }
    
  }

  // To open dialig box of Add TIme form.
  onAddTime(): void {
    const dialogRef: MatDialogRef<AddTimeComponent> = this.dialog.open(AddTimeComponent, {
      width: '40%',
      height: '730px',
      data: {},
      panelClass: 'custom-dialog-add-time',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  ontrueclick(leavid) {
    this.leavid = leavid;
    console.log(" this.leavid", this.leavid);
    //  this.approved = true;
    // this.denied = false;
    let body =
    {
      "status": "ACCEPTED"
    }

    this.timesheetService.approvalstatusById(body, this.currentUser.id, this.leavid).then((res) => {
      if (res) {
        this.approved = true;
        this.denied = false;
        this.toastService.openSnackBar("Leave Approval Successfully!!!");
        this.getleaveApprovalList();
      }

    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }

    });
  }

  onfalseclick(leavid) {
    this.leavid1 = leavid;
    console.log(" this.leavid", this.leavid1);
    // this.approved = false;
    // this.denied = true;
    let body1 =
    {
      "status": "REJECTED"
    }

    this.timesheetService.approvalstatusById(body1, this.currentUser.id, this.leavid1).then((res) => {
      if (res) {
        this.approved = false;
        this.denied = true;
        this.toastService.openSnackBar("Leave Denied Successfully!!!");
        this.getleaveApprovalList();
      }

    }, (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }

    });
  }

  getApprovaldeleteId(approvalId: any) {
    this.approvalId = approvalId;
  }

  deleteapprovalById() {
    this.timesheetService.deleteapprovalDeleteById({}, this.approvalId).then((res) => {
      if (res) {
        this.toastService.openSnackBar("Leave Approval Deleted Successfully!!!");
        this.getleaveApprovalList();
      }

    } , (err) => {
      if (err.error.expose) {
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }

    });
  }

  
  GotoImageOrPDF(imagelink){
    this.DataLink=imagelink
    const pdfName = "document.pdf";
    FileSaver.saveAs(this.DataLink, pdfName);


  }

}
